import {useEffect, lazy, Suspense, useState, useContext} from 'react';
import { Route, Switch, useRouteMatch, useHistory, Redirect, Link } from 'react-router-dom';
import { Icon } from 'react-icons-kit'
import {edit} from 'react-icons-kit/typicons/edit';
import {heartO} from 'react-icons-kit/fa/heartO';
import logo from './image/logo.svg'
import {useDispatch, useSelector} from 'react-redux';
import {getToken} from '../application/selectors/auth';
import { post, fetchData } from '../application/middlewares/index';
import {setCategory,setFilters} from "../application/actions/data";
import { getCategory,getFilters,getUser } from '../application/selectors/auth';
import Lang1 from '../pages/Language/Lang1';
import { LangContext } from "../pages/Language/LangContext";
import Input from "@material-tailwind/react/Input";
import Button from "@material-tailwind/react/Button";
import { yupResolver } from '@hookform/resolvers/yup';
import * as Yup from 'yup';
import { useForm, Controller} from "react-hook-form";
import Loading from '../components/Loading';
import toast from 'react-hot-toast';



const Map = lazy(() => import('./Map'))


function Update({update_id}){
  const { lang, dispatchLang } = useContext(LangContext);
 
    const dispatch = useDispatch();
    const history = useHistory();
    const categories = useSelector(getCategory);
    const user = useSelector(getUser);
    const token = useSelector(getToken);
    const [update, setUpdate] = useState();
    const [forms, setForms] = useState();
    const [loading, setLoading] = useState(true)
    const [filter, setFilter] = useState([]);
    const [filter1, setFilter1] = useState();
    const [location, setLocation]  = useState({});
    const [locations, setLocations]  = useState([]);
    const [locations_get, setLocations_get]  = useState([]);
    const [address_id, setAddress_id]  = useState();
    const [images, setImages] = useState(null);
    const [load_btn, setLoad_btn]  = useState(false);



    let dil='';
    if (lang=='ru') {dil='ru'} else {dil='tm'}
    const [values, setValues] = useState({})
    // console.log(update_id);
    const [position, setPosition]  = useState({});
    useEffect(()=>{
      // console.log(location.lat,"-----get1");
    setPosition({"lat":location?.lat,"lng":location?.lng});
  
    },[location]);
    // console.log(position,"-----++");

    useEffect(() => {
        dispatch(fetchData({
            url:`${dil}/main-locations`,
            token:'',
            action:(response)=>{
                if(response.success){
                    // console.log("get_location ",response)
                    // console.log(response.data.rows[0].real_estates_all[0])
                    setLocations(response.data.rows);
                    // console.log(products)
                } else {
                    console.log("error_data ",response);
                    }
                }
            }))
    }, [dil]);
    useEffect(() => {
        if (address_id){
        dispatch(fetchData({
            url:`${dil}/region-locations/${address_id}`,
            token:'',
            action:(response)=>{
                if(response.success){
                    // console.log("get_location ",response)
                    // console.log(response.data.rows[0].real_estates_all[0])
                    setLocations_get(response.data.rows);
                    // console.log(products)
                } else {
                    console.log("error_data ",response);
                    }
                }
            }))
        }
    }, [address_id, dil]);
    useEffect(() => {
        dispatch(fetchData({
            url:`user/${dil}/user-real-estate/${update_id}`,
            token: token,
            action:(response)=>{
                if(response.success){
                    // console.log("get_update ",response.data.rows);
                    setUpdate(response.data.rows);

                    // setValues({"price":response.data?.rows?.price, "area":response.data?.rows?.area})
                    setLoading(false)
                } else {
                    console.log("error_data ",response);
                    }
                }
            }))
    }, [dil]);
    useEffect(()=>{
        // console.log(location.lat,"-----get1");
        setPosition({"lat":update?.lat,"lng":update?.lng});
        setValue("price", update?.price)
        setValue("area", update?.area)
        setValue("description_tm", update?.description_tm)
        setValue("description_ru", update?.description_ru)
        setValue("location_id", update?.location_id);
    },[update]);



    useEffect(()=>{
        if (update?.type_id) {
        dispatch(fetchData({
            url:`${dil}/specifications-for-type/${update?.type_id}/${update?.category_id}`,
            token:'',
            action:(response)=>{
                if(response.success){
                    // console.log("get_form update ",response.data.rows)
                    // console.log(response.data.rows);
                    setForms(response.data.rows);
                } else {
                    console.log("error_data ",response);
                    }
                }
            }))}
    },[update, dil])
    
    const { register ,watch, handleSubmit, formState: { errors }, setError, setValue, getValues} = useForm({
        resolver: yupResolver(schema),
        // defaultValues:{...update}
    });
    
    
    useEffect(()=>{
        setFilter([])
        if(forms ){
            console.log(update,"--update")
            console.log(forms,"--forms")
            // console.log("I am in useEffect with forms")
            for(let i=0; i<forms.length; i++){
                setFilter(prevState => [...prevState, {"id":forms[i].specification_id, "is_required":forms[i].is_required, 
                    "is_multiple":forms[i].is_multiple, values:[]}])                
            }
           setFilter1(1); 
        }
        
    }, [forms, update])

    useEffect(()=>{
        if(filter?.length){
            for(let i=0; i < update?.specifications?.length; i++){
                let spec = update.specifications[i]
                setFilter(filter.filter(item=>{
                    if(item.id === spec.id){
                        item.values = spec.values.map(item => item.value_id)
                    }return item;
                }))
            }
        }
        console.log(filter)
    }, [filter1])
  
    function Valuee(value,value1){
        setFilter(filter.map((item)=>{
            if (item.id == value){
                item.values[0]=value1;
            }
            return item;
        }))

    }

    function Values_more(value,value1){
        setFilter(filter.map((item)=>{
            if (item.id == value){
                if(item.values.includes(value1)){
                    item.values = item.values.filter(item1 =>item1 !== value1)
                }else{
                    item.values.push(value1);
                }
            }
            return item;
        }))
        // console.log(value,"----1");
        // console.log(value,"----2");
    }
        var selects = document.querySelectorAll(".radio-toolbar");
        selects.forEach(function(item) {
        // console.log(item.children[0]);
        if(item.children[0].checked) {
        let q= item.classList.add("toolbar");
        } else {
        let q= item.classList.remove("toolbar");
        }
        });

    // console.log(filter,'-------filter');
    const ChangeImage = (e) =>{
        setImages([]);
        setImages(e.target.files)
        // console.log(e.target.files)
        // setImages(e.target.files[0])
        // setImages(e.target.files)
    }

    const onSubmit = (data) => {
        data={...data, "specifications":filter, "position":position}
        console.log(data,"----useForm hook");
        setLoad_btn(true);
        dispatch(post({
            url:`user/${dil}/update-real-estate/${update?.real_estate_id}`,
            token: token,
            data: data,
            action:(response)=>{
                if(response.success){
                    console.log("update_response ",response)
                    toast.success(Lang1[lang].update);
                    setLoad_btn(false)
                    history.push('/')
                    // console.log(response.data.rows);
                    // setForms(response.data.rows);
                } else {
                    console.log("error_data ",response);
                    toast.error(Lang1[lang].errors);
                    setLoad_btn(false)
                    if (response?.message?.data){
                        let keys = Object?.keys(response?.message?.data?.error);
                        // console.log(keys,"------keys");
                        keys?.map(item => {
                            setError(item, {type:'manual', message:response?.message?.data?.error[item]})
                        })
                        // console.log(keys)
                     }
                    }
                }
            }))

    }
   

    return(
        <>
        {loading ? <div className='my-6 w-full flex items-center justify-center bg-white h-screen'><Loading type="table"/></div> : 
        
        <form onSubmit={handleSubmit(onSubmit)}
         className="profile_main flex flex-col bg-white border rounded-md my-6 p-3">
             {update?.rejections != null
             ?
             <>
                 <h1 className='text-red-500'>{Lang1[lang].rejected}</h1>
                    <p>{update?.rejections[(update?.rejections.length)-1]?.comment}</p>
             </>
            :
            null
            }
            
            <div className="w-full flex flex-col sm:flex-row justify-between mt-5">
                <div className="title_name1">
                    <p>{Lang1[lang].not15}</p>
                </div>
                <div className="filter_links1">
                <input type="text" {...register("price")} name="price"  
                className="p-1 px-2 border rounded-sm w-40 mr-4 focus:outline-none focus:ring-2 focus:ring-blue-600 focus:ring-opacity-50" />
                <span>{Lang1[lang].not16}</span>
                <p className="text-red-500 my-1">{errors.price?.message ? errors.price.message : null}</p>
                </div>        
            </div>
            <div className="w-full flex flex-col sm:flex-row justify-between mt-5">
                <div className="title_name1">
                    <p>{Lang1[lang].not17}</p>
                </div>
                <div className="filter_links1">
                    <input type="text" {...register("area")} name="area"
                    className="p-1 px-2 border rounded-sm w-40 mr-4 focus:outline-none focus:ring-2 focus:ring-blue-600 focus:ring-opacity-50" />
                    <span></span>
                    <p className="text-red-500 my-1">{errors.area?.message ? errors.area.message : null}</p>

                </div>
            </div>
            <div className="w-full flex flex-col sm:flex-row justify-between mt-5">
                <div className="title_name1">
                            <p>{Lang1[lang].not18}</p>
                </div>
                <div className="filter_links1">
                    <textarea {...register("description_tm")} name="description_tm"
                    className="border p-1 w-full sm:w-1/2 h-44 text-sm px-2 focus:outline-none focus:ring-2 focus:ring-blue-600 focus:ring-opacity-50"></textarea>
                    {/* <p className="text-red-500 my-1">{errors.descriptions?.message ? errors.descriptions.message : null}</p> */}
                    <p className="text-red-500 my-1">{errors.description_tm?.message ? errors.description_tm.message : null}</p>
                </div>
            </div>
            <div className="w-full flex flex-col sm:flex-row justify-between mt-5">
                <div className="title_name1">
                    <p>{Lang1[lang].not19}</p>
                </div>
                <div className="filter_links1">
                    <textarea {...register("description_ru")} name="description_ru"
                    className="border p-1 w-full sm:w-1/2 h-44 text-sm px-2 focus:outline-none focus:ring-2 focus:ring-blue-600 focus:ring-opacity-50"></textarea>
                    {/* <p className="text-red-500 my-1">{errors.descriptions?.message ? errors.descriptions.message : null}</p> */}
                    <p className="text-red-500 my-1">{errors.description_ru?.message ? errors.description_ru.message : null}</p>
                </div>
            </div>
            {forms?.map((item)=>(
                        <div className="w-full flex flex-col sm:flex-row justify-between mt-5">
                        <div className="title_name1">
                            <p key={item.id}>{item.name}</p>
                        </div>
                        <div className="filter_links1">
                            <div className="flex flex-wrap">
                                {item.is_multiple ?
                                <div className="flex flex-wrap">
                                {item?.values?.map((item_value)=>(
                                    filter.map(filt => {
                                        if(filt.id === item.specification_id){
                                           if (filt.values.includes(item_value.value_id)) {
                                               return (
                                            <label
                                                className="toolbar mb-3 border outline-none cursor-pointer rounded-sm p-1 px-3 
                                                hover:text-white hover:bg-blue-600 mr-4 flex items-center" for={item_value.value_id}>
                                                <input type="checkbox" checked   id={item_value.value_id} className="hidden" onClick={() =>{
                                                Values_more(item.specification_id,item_value.value_id);
                                                var selects = document.querySelectorAll(".radio-toolbar");
                                                selects.forEach(function(item) {
                                                // console.log(item.children[0]);
                                                if(item.children[0].checked) {
                                                 let q= item.classList.add("toolbar");
                                                    } else {
                                                let q= item.classList.remove("toolbar");
                                                    }
                                                    });
                                                    }} />
                                                    <span key={item_value.value_id}>{item_value.absolute_value}</span>
                                             </label>
                                               )
                                           }
                                           else {
                                            return (
                                                <label
                                                    className="radio-toolbar mb-3 border outline-none cursor-pointer rounded-sm p-1 px-3 
                                                    hover:text-white hover:bg-blue-600 mr-4 flex items-center" for={item_value.value_id}>
                                                    <input type="checkbox"   id={item_value.value_id} className="hidden" onClick={() =>{
                                                    Values_more(item.specification_id,item_value.value_id);
                                                    var selects = document.querySelectorAll(".radio-toolbar");
                                                    selects.forEach(function(item) {
                                                    // console.log(item.children[0]);
                                                    if(item.children[0].checked) {
                                                     let q= item.classList.add("toolbar");
                                                        } else {
                                                    let q= item.classList.remove("toolbar");
                                                        }
                                                        });
                                                        }} />
                                                        <span key={item_value.value_id}>{item_value.absolute_value}</span>
                                                 </label>
                                                   )
                                           }
                                        }
                                    })
                                    
                                ))}
                                </div>
                                :
                                <div className="flex flex-wrap">
                                {item?.values?.map((item_value)=>(
                                     filter.map(filt => {
                                        if(filt.id === item.specification_id){
                                           if (filt.values.includes(item_value.value_id)) {
                                               return (
                                            <label
                                                className="toolbar mb-3 border outline-none cursor-pointer rounded-sm p-1 px-3 
                                                hover:text-white hover:bg-blue-600 mr-4 flex items-center" for={item_value.value_id}>
                                                <input type="radio" checked name={item.name} id={item_value.value_id} className="hidden" onClick={() =>{
                                                Valuee(item.specification_id,item_value.value_id);
                                                var selects = document.querySelectorAll(".radio-toolbar");
                                                selects.forEach(function(item) {
                                                // console.log(item.children[0]);
                                                if(item.children[0].checked) {
                                                 let q= item.classList.add("toolbar");
                                                    } else {
                                                let q= item.classList.remove("toolbar");
                                                    }
                                                    });
                                                    }} />
                                                    <span key={item_value.value_id}>{item_value.absolute_value}</span>
                                             </label>
                                                )
                                            }
                                            else {
                                                return (
                                                    <label
                                                        className="radio-toolbar mb-3 border outline-none cursor-pointer rounded-sm p-1 px-3 
                                                        hover:text-white hover:bg-blue-600 mr-4 flex items-center" for={item_value.value_id}>
                                                        <input type="radio" name={item.name} id={item_value.value_id} className="hidden" onClick={() =>{
                                                        Valuee(item.specification_id,item_value.value_id);
                                                        var selects = document.querySelectorAll(".radio-toolbar");
                                                        selects.forEach(function(item) {
                                                        // console.log(item.children[0]);
                                                        if(item.children[0].checked) {
                                                         let q= item.classList.add("toolbar");
                                                            } else {
                                                        let q= item.classList.remove("toolbar");
                                                            }
                                                            });
                                                            }} />
                                                            <span key={item_value.value_id}>{item_value.absolute_value}</span>
                                                     </label>
                                                        )
                                            }
                                         }
                                     })
                                      
                                ))}
                                </div>
                                }
                                <p className="text-red-500 my-1 ml-2">
                            {errors.specifications?.message ? errors.specifications.message : null}
                        </p>
                            </div>
                        </div>
                    </div>
                    ))}
                    <div className="w-full flex flex-col sm:flex-row justify-between mt-5">
                        <div className="title_name1">
                            <p>{Lang1[lang].not30}</p>
                        </div>
                        <div className="filter_links1">
                            <div className="flex flex-wrap">

                            {locations?.map((item)=>(
                                <label className="radio_loc mb-3 rounded-md border outline-none cursor-pointer rounded-sm p-1 px-3 
                                hover:text-white hover:bg-blue-600 mr-4" onClick={()=>{
                                    setAddress_id(item.id);
                                    var selects = document.querySelectorAll(".radio_loc");
                                    selects.forEach(function(item) {
                                        // console.log(item.children[0]);
                                        if(item.children[0].checked) {
                                            let q= item.classList.add("toolbar");
                                            } else {
                                                let q= item.classList.remove("toolbar");
                                            }
                                      });
                                }}>
                                    <input type="radio" name="loc" className="hidden" />
                                    <span>{item.name}</span>
                                </label>                                
                            ))}
                            <p className="text-red-500 my-1">{errors.location_id?.message ? errors.location_id.message : null}</p>
                            {/* <p className="text-red-500 my-1">{errors.loc?.message ? errors.loc.message : null}</p> */}
                            </div>

                            <hr />
                            <div className="location_grid">
                            <div className="flex flex-col ">
                            {locations_get?.slice(0,7).map((item)=>(
                                <label className="my-1" onClick={()=>{
                                    setValue("location_id", item.id);
                                }}>
                                    <input type="radio" name="loc1" className="mr-3" />
                                    <span>{item.name}</span>
                                </label>
                            ))}
                            </div>
                            <div className="flex flex-col ">
                            {locations_get?.slice(7,15).map((item)=>(
                                <label className="my-1" onClick={()=>{
                                    setValue("location_id", item.id);
                                }}>
                                    <input type="radio" name="loc1" className="mr-3" />
                                    <span>{item.name}</span>
                                </label>
                            ))}
                            </div>
                            <div className="flex flex-col ">
                            {locations_get?.slice(15,23).map((item)=>(
                                <label className="my-1" onClick={()=>{
                                    setValue("location_id", item.id);
                                }}>
                                    <input type="radio" name="loc1" className="mr-3" />
                                    <span>{item.name}</span>
                                </label>
                            ))}
                            </div>
                            <div className="flex flex-col ">
                            {locations_get?.slice(23,30).map((item)=>(
                                <label className="my-1" onClick={()=>{
                                    setValue("location_id", item.id);
                                }}>
                                    <input type="radio" name="loc1" className="mr-3" />
                                    <span>{item.name}</span>
                                </label>
                            ))}
                            </div>
                            
                            </div>

                            
                        </div>
                    </div>
                    <div className="w-full flex flex-col sm:flex-row justify-between mt-5">
                        <div className="title_name1">
                            <p>{Lang1[lang].not20}</p>
                        </div>
                        <div className="filter_links1">
                            <div className="w-full sm:w-1/2 h-40 border flex justify-center items-center relative">
                                <input type="file" name="image" onChange={ChangeImage} multiple />
                                <div onClick={()=> {
                                    // setPage_router(3);
                                    // setUpdate_id(item.id);
                                }}
                                className="edit shadow-lg cursor-pointer rounded-md bg-blue-100 z-1000">
                                    <Icon size={24} icon={edit} className="text-blue-600 ml-1 mb-1" />
                                </div>
                            </div>
                            <p className="text-sm text-gray-600">{Lang1[lang].not21}</p>
                            {/* <p className="text-red-500 my-1">{errors.image?.message ? errors.image.message : null}</p> */}
                        </div>
                    </div>
                    <hr className="my-8"/>
                    <p className="font-medium my-3">{Lang1[lang].not22}</p>
                    {/* <input type="text" placeholder="Turkmenistan, Ashgabat" className="p-1 px-2 border rounded-sm mb-3" /> */}
                    <Map setLocation={setLocation} position={position} />
                    <p className="text-red-500 my-1">{errors.position?.message ? errors.position.message : null}</p>

            <Button
                color="lightBlue"
                buttonType="link"
                size="regular"
                rounded={false}
                block={false}
                iconOnly={false}
                ripple="dark"
                type="submit"
                className="w-40 flex self-center mb-5 mt-8"
            >
                {load_btn ?
                <Loading type="button" />
                :
                <span className='text-xs'>
                Update
                </span>    
                 }
            </Button>
        </form>
    }
    </>
    )
}

const schema =  Yup.object().shape({
    // category_id:Yup.string().required("Hokman bolmaly"),
    // type_id:Yup.string().required("Hokman bolmaly"),
    price:Yup.string().max(10, "10 belgiden az bolmaly").required("Hokman bolmaly"),
    area:Yup.string().max(5, "5 belgiden az bolmaly").required("Hokman bolmaly"),
    // image:Yup.string().required("Hokman bolmaly"),
    // position:Yup.string().required("Hokman yerleshyan yeri bolmaly"),
    description_tm:Yup.string().min(10, "Azyndan 10 simwol bolmaly").required("Hokman bolmaly"),
    description_ru:Yup.string().min(10, "Azyndan 10 simwol bolmaly").required("Hokman bolmaly"),
    // location_id:Yup.string().required("Hokman bolmaly"),
    
  });

export default Update;